import React, { useEffect, useState } from 'react';

import pcVideo from '../../assets/main-movie-pc.mp4';
import spVideo from '../../assets/main-movie-sp.mp4';
import pcPoster from '../../images/main-movie-pc-thumb.jpg';
import spPoster from '../../images/main-movie-sp-thumb.jpg';

interface MainvisualProps extends React.HTMLProps<HTMLDivElement> {}

const Mainvisual: React.FC<MainvisualProps> = () => {
  const [clientWidth, setClientWidth] = useState(0);
  const [clientHeight, setClientHeight] = useState(0);
  const [videoSrc, setVideoSrc] = useState(undefined);
  const [videoPoster, setVideoPoster] = useState('');
  const [videoWidth, setVideoWidth] = useState(0);
  const [videoHeight, setVideoHeight] = useState(0);
  const [displayVideoWidth, setDisplayVideoWidth] = useState(0);
  const [displayVideoHeight, setDisplayVideoHeight] = useState(0);
  const [marginLeft, setMarginLeft] = useState(0);
  const [isDisplaySlogan, setIsDisplaySlogan] = useState(true);

  const updateSlogan = () => {
    const scrollY: number =
      document.documentElement.scrollTop || window.pageYOffset;

    setIsDisplaySlogan(scrollY < window.outerHeight + 1);
  };

  const resizeVideo = () => {
    /*
    const clientWidth: number = Math.max(document.documentElement.clientWidth, window.innerWidth)
    const clientHeight: number = Math.max(document.documentElement.clientHeight, window.innerHeight)
     */

    // Safari のバーも含めて計算したいため
    const __clientWidth: number = window.outerWidth;
    const __clientHeight: number = window.outerHeight;

    let __videoWidth: number,
      __videoHeight: number,
      __displayVideoWidth: number,
      __displayVideoHeight: number,
      __marginLeft: any;

    const isLandscape: boolean = __clientHeight < __clientWidth;

    setVideoSrc(isLandscape ? pcVideo : spVideo);
    setVideoPoster(isLandscape ? pcPoster : spPoster);

    __videoWidth = isLandscape ? 1280 : 320;
    __videoHeight = isLandscape ? 720 : 440;

    if (__clientWidth / __clientHeight < __videoWidth / __videoHeight) {
      // ウィンドウの方が高さが余るため、動画の縦を基準にする
      __displayVideoHeight = __clientHeight;
      __displayVideoWidth =
        (__displayVideoHeight / __videoHeight) * __videoWidth;
      __marginLeft = (__displayVideoWidth - __clientWidth) * -0.5;
    } else {
      // ウィンドウの方が左右が余るため、動画の横を基準にする
      __displayVideoWidth = __clientWidth;
      __displayVideoHeight =
        (__displayVideoWidth / __videoWidth) * __videoHeight;
      __marginLeft = 0;
    }

    setClientWidth(__clientWidth);
    setClientHeight(__clientHeight);
    setVideoWidth(__videoWidth);
    setVideoHeight(__videoHeight);

    const floor = (value: number, digit: number = 10) =>
      Math.floor(value * digit) / digit;

    setDisplayVideoWidth(floor(__displayVideoWidth));
    setDisplayVideoHeight(floor(__displayVideoHeight));
    setMarginLeft(floor(__marginLeft));
  };

  const handleScrollWindow = () => {
    updateSlogan();
  };

  const handleResizeWindow = () => {
    resizeVideo();
    // リサイズ時にサイズが取れていない場合があるため念のためディレイ実行もさせる
    setTimeout(resizeVideo, 1000);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScrollWindow);
    window.addEventListener('resize', handleResizeWindow);
    updateSlogan();
    resizeVideo();

    return () => {
      window.removeEventListener('scroll', handleScrollWindow);
      window.removeEventListener('resize', handleResizeWindow);
    };
  }, []);

  return (
    <div
      className='mainvisual'
      style={{
        // 100vw, 100vh 指定だと Safari のバーを含めて計算してしまうためJSで
        /*
          width: clientWidth,
          height: clientHeight
           */
        width: '100vw',
        height: '100vh'
      }}
    >
      <div className='container'>
        <h1
          className={`mainvisual__slogan text-black px-3 py-2${
            isDisplaySlogan ? '' : ' d-none'
          }`}
          style={{
            fontFamily: 'YuMincho, serif'
          }}
        >
          <div>
            <span className='text-nowrap'>最高の時間を、</span>
            <span className='text-nowrap'>最適な空間と。</span>
          </div>

          <div className='mt-5'>
            <small>
              <span className='bg-white p-2'>コンパクトウシ</span> のススメ
            </small>
          </div>
        </h1>
      </div>
      <video
        style={{
          width: displayVideoWidth,
          height: displayVideoHeight,
          left: marginLeft
        }}
        className='mainvisual__video'
        src={videoSrc}
        poster={videoPoster}
        width={videoWidth}
        height={videoHeight}
        autoPlay={false}
        muted={true}
        loop={true}
        playsInline={true}
      />
    </div>
  );
};

export default Mainvisual;
